html, body {
  margin: 0;
  padding: 0;
  font-family: Open Sans, sans-serif;
}

a {
  color: #1b70c4;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

#content {
  width: 80%;
  margin: 10px auto;
  padding: 10px;
}

h1 {
  opacity: .87;
}

#root {
  flex-direction: row;
  display: flex;
}

#use-a-catcher {
  text-align: center;
  min-width: 40%;
  border-radius: 5px;
  margin: 2em 3% 0;
  padding: 1em;
  font-size: 1.5em;
  font-weight: lighter;
}

#use-a-catcher input#subdomain {
  width: 5em;
  text-align: center;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-family: Open Sans, sans-serif;
  font-size: 1em;
  font-weight: lighter;
}

#new-catcher input#go {
  color: #fff;
  background: #15cd72;
  border: none;
  border-radius: 5px;
  margin-top: 2em;
  padding: .75rem 3rem;
  font-size: .7em;
}

#new-catcher input#go:hover {
  cursor: pointer;
  background: #0cb863;
}

#new-catcher #go-row {
  margin: .5em 0;
}

h4 {
  font-size: 20pt;
}

h2 {
  font-weight: lighter;
}

#explanation {
  flex: 1;
  margin: 50px 0 20px;
}

div.options a {
  color: #7da5ad;
  margin: 1em 0;
  font-size: .8em;
}

